.blog {
  max-width: 100%;
  width: 100%;

  &__links {
    color: $color_grey-dark;
    font-size: $font_size-12;
    margin-bottom: 2rem;

    @media only screen and (min-width: 1104px) {
      font-size: $font_size-16;
    }
  }

  &__links__item {
    text-decoration: none;
    color: $color_grey-dark;
  }

  &__btn {
    margin-bottom: 2rem;
    padding: 13px 26px;
    text-transform: uppercase;
    font-weight: $font_weight-bold;

    @media only screen and (min-width: 1104px) {
      font-size: $font_size-16;
    }
  }

  &__info {
    @media only screen and (min-width: 400px) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__title {
    font-family: 'AmsiProNarw-BlackItalic';
    font-size: $font_size-30;
    text-align: center;
    margin-bottom: 2rem;

    @media only screen and (min-width: 1104px) {
      font-size: $font_size-37;
    }
  }

  &__img {
    width: 100%;
  }

  &__subtitle {
    text-transform: uppercase;
    font-weight: $font_weight-bold;
    line-height: 1.5;
    margin-bottom: 2rem;
    margin-top: 4rem;

  }

  @media only screen and (min-width: 768px) {
    max-width: 768px;
  }

  @media only screen and (min-width: 1104px) {
    max-width: 1104px;
  }

  @media only screen and (min-width: 1301px) {
    max-width: 818px;
  }
}

.quotation {
  display: flex;
  margin-top: 2.5rem;

  @media only screen and (min-width: 1104px) {
    margin: 4.2rem 0;
  }

  &__img {
    width: 2rem;
    align-self: flex-start;
    margin-right: 1rem;

    @media only screen and (min-width: 1104px) {
      width: 4rem;
    }

    &:last-child {
      align-self: flex-end;
      transform: rotate(180deg);
    }
  }

  & > .paragraph__text {
    margin-bottom: 0;
    margin-top: 0;
  }
}

.instagram-posts-wrapper {
  background-color: transparentize($color_grey, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
  margin-top: 2.5rem;
  margin-bottom: 3rem;

  & .instagram-post:last-child {
    margin-bottom: 0;
  }

  @media only screen and (min-width: 1104px) {
    padding: 5rem 1rem;
  }

  & > .paragraph__text {
    text-align: center;
    margin-top: 0.8rem;
    margin-bottom: 3rem;
    padding: 0 0.9rem;

    @media only screen and (min-width: 1104px) {
      margin-top: 0;
      margin-bottom: 5rem;
    }
  }
}
